<template v-bar>
  <v-row class="fill-height">
    <v-col>
      <v-sheet>
        <v-toolbar flat>
          <v-btn
            outlined
            class="mr-0"
            min-width="5%"
            color="grey darken-2"
            @click="monthPicker = true"
          >
            Mes
          </v-btn>
          <v-btn fab text small color="grey darken-2" @click="prev" v-if="!mobile">
            <v-icon small> mdi-chevron-left </v-icon>
          </v-btn>
          <v-btn fab text small color="grey darken-2" @click="next" v-if="!mobile">
            <v-icon small> mdi-chevron-right </v-icon>
          </v-btn>
          <v-toolbar-title class="d-none d-sm-block" v-if="$refs.calendar && !mobile">
            {{ $refs.calendar.title }}
          </v-toolbar-title>
          <v-spacer></v-spacer>
          <v-spacer></v-spacer>
          <v-btn
            id="btn-cas"
            class="btn-cas mr-2 clase-prueba"
            style="padding-left: 8px; padding-right: 8px"
            color="green"
            @click="dialogMes = true"
            dark
          >
            <span>Exportar</span>
          </v-btn>
          <v-menu bottom right>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                outlined
                color="grey darken-2"
                v-bind="attrs"
                v-on="on"
                style="padding-left: 8px; padding-right: 8px"
              >
                <span>{{ typeToLabel[type] }}</span>
                <v-icon right> mdi-menu-down </v-icon>
              </v-btn>
            </template>
            <v-list>
              <v-list-item @click="type = 'day'">
                <v-list-item-title>Da</v-list-item-title>
              </v-list-item>
              <v-list-item v-if="!mobile" id="view-c" @click="type = 'week'">
                <v-list-item-title>Semana</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </v-toolbar>
        <v-toolbar
          flat
          v-if="mobile"
          style="display: flex; justify-content: center"
        >
          <v-btn fab text color="grey darken-2" @click="prev" class="mr-3">
            <v-icon> mdi-chevron-left </v-icon>
          </v-btn>
          <v-toolbar-title v-if="$refs.calendar">
            {{ $refs.calendar.title }}
          </v-toolbar-title>
          <v-btn fab text color="grey darken-2" @click="next" class="ml-3">
            <v-icon> mdi-chevron-right </v-icon>
          </v-btn>
        </v-toolbar>
      </v-sheet>
      <v-sheet height="100%">
        <v-calendar
          id="cal"
          ref="calendar"
          :interval-format="intervalFormat"
          v-model="focus"
          color="primary"
          :events="eventos"
          :weekdays="weekday"
          :event-color="getEventColor"
          :type="type"
          @click:event="showEvent"
          @click:more="viewDay"
          @click:date="viewDay"
          event-overlap-mode="column"
        >
          <template v-slot:day-body="{ date, week }">
            <div
              class="v-current-time"
              :class="{ first: date === week[0].date }"
              :style="{ top: nowY }"
            ></div>
          </template>
          <template v-slot:event="{ event }">
            <div class="v-event-draggable pl-1 pt-1">
              <strong>{{ event.name }}</strong
              ><br />
              {{ formatEventTime(event.start) }} -
              {{ formatEventTime(event.end) }}
            </div>
          </template>
        </v-calendar>
        <v-menu
          v-model="selectedOpen"
          :close-on-content-click="false"
          :activator="selectedElement"
          offset-x
        >
          <v-card color="grey lighten-4" min-width="350px" flat>
            <v-toolbar :color="selectedEvent.color" dark>
              <v-toolbar-title v-html="selectedEvent.name"></v-toolbar-title>
              <v-spacer></v-spacer>
            </v-toolbar>
            <v-card-text>
              <span style="color: black" class="subheading font-weight-bold"
                >Cdula: </span>
              <span class="text--primary" v-html="selectedEvent.cedula"></span>
              <br />
              <span style="color: black" class="subheading font-weight-bold"
                >rea o servicio: </span
              >
              <span class="text--primary" v-html="selectedEvent.details1"></span
              ><br />
              <span style="color: black" class="subheading font-weight-bold"
                >Centro de costo: </span
              >
              <span
                class="text--primary"
                v-html="selectedEvent.details2"
              ></span>
              <br />
              <span style="color: black" class="subheading font-weight-bold"
                >Tipo de contrato: </span>
              <span class="text--primary" v-html="selectedEvent.tipocontrato"></span>
              <br />
              <span style="color: black" class="subheading font-weight-bold"
                >Cargo: </span>
              <span class="text--primary" v-html="selectedEvent.cargo"></span>
            </v-card-text>
            <v-card-actions>
              <v-btn text color="secondary" @click="selectedOpen = false">
                Cerrar
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-menu>
      </v-sheet>

      <!-- Filtrado de funcionario -->
      <!-- <FilterDialog></FilterDialog> -->
      <DialogHistory v-bind:arrayFuncionarios="funcionarios"></DialogHistory>

      <v-dialog v-model="dialogImport" hide-overlay persistent width="300">
        <v-card color="blue darken-4" dark>
          <v-card-text>
            Importando horarios...
            <v-progress-linear
              rounded
              indeterminate
              color="white"
              class="mb-0"
            ></v-progress-linear>
          </v-card-text>
        </v-card>
      </v-dialog>

      <v-dialog
        v-if="dialogMes"
        v-model="dialogMes"
        scrollable
        max-width="300px"
      >
        <v-card>
          <v-card-title>Seleccione el mes</v-card-title>
          <v-divider></v-divider>
          <v-card-text>
            <v-radio-group v-model="opcionMes" column>
              <v-radio
                v-for="mes in meses"
                :key="mes.id"
                :value="mes.id"
                :label="mes.name"
              ></v-radio>
            </v-radio-group>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-actions>
            <v-btn color="blue darken-1" text @click="dialogMes = false">
              Cerrar
            </v-btn>
            <v-btn color="blue darken-1" text @click="exportExcel()">
              Exportar horario
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-dialog v-model="monthPicker" v-if="monthPicker" max-width="290px">
        <v-card>
          <v-date-picker
            v-model="date"
            type="month"
            min="2021-01"
            max="2021-12"
          ></v-date-picker>
          <v-divider></v-divider>
          <v-card-actions>
            <v-btn color="blue darken-1" text @click="monthPicker = false">
              Cerrar
            </v-btn>
            <v-btn color="blue darken-1" text @click="setToday()">
              Aceptar
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-snackbar v-model="snackbarExport" :timeout="timeout" color="#0061aa">
        {{ text }}
        <template v-slot:action="{ attrs }">
          <v-btn
            color="white"
            text
            v-bind="attrs"
            @click="snackbarExport = false"
          >
            Cerrar
          </v-btn>
        </template>
      </v-snackbar>
    </v-col>
  </v-row>
</template>

<script>
import XLSX from "xlsx";
import DialogHistory from "../components/DialogHistory.vue";
import { mapActions, mapState, mapMutations } from "vuex";

export default {
  components: {
    DialogHistory,
  },
  props: {
    title: {
      type: String,
    },
  },
  data() {
    return {
      text: "",
      timeout: 2000,
      snackbarExport: false,
      monthPicker: false,
      date: "",
      opcion: "",
      opcionMes: "",
      dialogImport: false,
      // funcionarios: [],
      backup: [],
      events: [],
      headers: [
        {
          text: "Nombre funcionario",
          align: "start",
          sortable: false,
          value: "name",
        },
      ],
      dialogMes: false,
      dialog: false,
      dialogItem: false,
      dialogGen: false,
      fechaInicio: "",
      fechaFin: "",
      ready: false,
      dataToExport: [],
      focus: "",
      type: "week",
      typeToLabel: {
        month: "Month",
        week: "Semana",
        day: "Da",
        "4day": "4 Days",
      },
      selectedEvent: {},
      selectedElement: null,
      selectedOpen: false,
      weekday: [1, 2, 3, 4, 5, 6, 0],
      info: [],
      colors: [
        "blue",
        "indigo",
        "deep-purple",
        "cyan",
        "green",
        "orange",
        "grey darken-1",
      ],
      names: [
        "Meeting",
        "Holiday",
        "PTO",
        "Travel",
        "Event",
        "Birthday",
        "Conference",
        "Party",
      ],
      meses: [
        { id: "01", name: "Enero" },
        { id: "02", name: "Febrero" },
        { id: "03", name: "Marzo" },
        { id: "04", name: "Abril" },
        { id: "05", name: "Mayo" },
        { id: "06", name: "Junio" },
        { id: "07", name: "Julio" },
        { id: "08", name: "Agosto" },
        { id: "09", name: "Septiembre" },
        { id: "10", name: "Octubre" },
      ],
    };
  },
  computed: {
    ...mapState(["eventos", "funcionarios", "backup_eventos", "backup_funcionarios"]),
    cal() {
      return this.ready ? this.$refs.calendar : null;
    },
    nowY() {
      return this.cal ? this.cal.timeToY(this.cal.times.now) + "px" : "-10px";
    },
    mobile() {
      if (this.$vuetify.breakpoint.xs) {
        this.type = "day";
        return true;
      } else {
        this.type = "week";
        return false;
      }
    },
  },
  mounted() {
    this.ready = true;
    this.scrollToTime();
    this.updateTime();
    this.$refs.calendar.checkChange();
    this.getShifts();
  },
  methods: {
    ...mapActions([
      "updateEventos",
      "updateFuncionarios",
      "updateBackupEventos",
      "updateBackupFuncionarios",
    ]),
    ...mapMutations([
      "setEventos",
      "setFuncionarios",
      "setBackupEventos",
      "setBackupFuncionarios",
    ]),
    filtrarCalendario(opcion) {
      if (opcion == "Todos") {
        this.$store.dispatch("updateEventos", this.backup_eventos);
      } else {
        const found = this.funcionarios.find(
          (element) => element.name == opcion
        );
        this.$store.dispatch("updateEventos", found.horarios);
      }
      this.dialog = false;
    },
    intervalFormat(interval) {
      return interval.time;
    },
    formatEventTime(date) {
      return new Date(date).toLocaleTimeString("en-US", {
        hour: "2-digit",
        minute: "2-digit",
        hour12: false,
      });
    },
    getShifts() {
      this.dialogImport = true;
      this.axios
        .get(this.title)
        .then((response) => {
          // Respuesta del servidor
          if (response.data.length != 0) {
            response.data.forEach((element) => {
              element.cedula = `${element.Cedula}`;
              element.start = `${element.FechaInicio} ${element.HoraInicio}`;
              element.end = `${element.FechaFin} ${element.HoraFin}`;
              element.name = `${element.Nombre} ${element.Apellido}`;
              element.details1 = `${element.Area}`;
              element.details2 = `${element.CentroDeCosto}`;
              element.tipocontrato = `${element.TipoContrato}`;
              element.cargo = `${element.TipoCargo}`;
              if (element.TipoTurno == 1) {
                element.color = "green";
              } else if (element.TipoTurno == 2) {
                element.color = "light-blue darken-4";
              } else if (element.TipoTurno == 3) {
                element.color = "#9a67ea";
              } else if (element.TipoTurno == 4) {
                element.color = "deep-orange darken-4";
              } else if (element.TipoTurno == 5) {
                element.color = "yellow";
              }
            });

            // this.events = response.data;
            // this.backup = response.data;

            this.$store.dispatch("updateEventos", response.data);
            this.$store.dispatch("updateBackupEventos", response.data);

            let funcionarios = [];
            let nombre = "";
            let registros = [];
            let count = 0;
            for (let i = 0; i < response.data.length; i++) {
              nombre = response.data[i].name;
              if (!registros.includes(nombre)) {
                registros.push(nombre);
                funcionarios.push({
                  id: response.data[i].idFuncionarios,
                  name: response.data[i].name,
                  horarios: [],
                });
                response.data.forEach((element) => {
                  if (element.name == funcionarios[count].name) {
                    funcionarios[count].horarios.push({
                      idTurno: `${element.idTurnos}`,
                      id: `${element.idFuncionarios}`,
                      cedula: `${element.cedula}`,
                      name: `${element.name}`,
                      details1: `${element.details1}`,
                      details2: `${element.details2}`,
                      tipocontrato: `${element.tipocontrato}`,
                      cargo: `${element.cargo}`,
                      start: `${element.start}`,
                      end: `${element.end}`,
                      FechaInicio: `${element.FechaInicio}`,
                      color: `${element.color}`,
                    });
                  }
                });
                count++;
              }
            }
            //this.funcionarios = funcionarios;
            this.$store.dispatch("updateFuncionarios", funcionarios);
            this.$store.dispatch("updateBackupFuncionarios", funcionarios);
            this.dialogImport = false;
            this.snackbarExport = true;  //Reutilizando el snackbar de exportar
            this.text = "Los horarios fueron importados correctamente!";
          } else {
            this.dialogImport = false;
            this.snackbarExport = true;  //Reutilizando el snackbar de exportar
            this.text = "No existen horarios registrados para esta rea o servicio";
          }
        })
        .catch((e) => {
          console.log(e);
        });
    },
    getCurrentTime() {
      return this.cal
        ? this.cal.times.now.hour * 60 + this.cal.times.now.minute
        : 0;
    },
    scrollToTime() {
      const time = this.getCurrentTime();
      const first = Math.max(0, time - (time % 30) - 30);

      this.cal.scrollToTime(first);
    },
    updateTime() {
      setInterval(() => this.cal.updateTimes(), 60 * 1000);
    },
    exportExcel() {
      if (this.opcionMes != "") {
        let eventos = JSON.parse(JSON.stringify(this.backup_eventos));
        let exportar = [];
        eventos.forEach((element) => {
          delete element.idTurnos;
          delete element.idFuncionarios;
          delete element.name;
          delete element.start;
          delete element.end;
          delete element.details1;
          delete element.details2;
          delete element.color;
          delete element.HoraInicio;
          delete element.HoraFin;
          delete element.TipoContrato;
          delete element.TipoCargo;
          delete element.cedula;
          delete element.tipocontrato;
          delete element.cargo;
        });
        console.log(eventos)

        eventos.forEach((element) => {
          let mes1 = element.FechaInicio.split("-")[1];
          let mes2 = element.FechaFin.split("-")[1];

          if (this.opcionMes == mes1) {
            let tipoturno = element.TipoTurno;
            if (tipoturno == 1) {
              element.TipoTurno = "C";
            } else if (tipoturno == 2) {
              element.TipoTurno = "N";
            } else if (tipoturno == 3) {
              element.TipoTurno = "JC";
            } else if (tipoturno == 4) {
              element.TipoTurno = "M";
            } else if (tipoturno == 5) {
              element.TipoTurno = "INC";
            }
            exportar.push(element);
          }
        });

        if (exportar.length != 0) {
          let data = XLSX.utils.json_to_sheet(exportar);
          const workbook = XLSX.utils.book_new();
          const filename = `Calendario_Enfermeria_Mes_${this.opcionMes}`;
          XLSX.utils.book_append_sheet(workbook, data, filename);
          XLSX.writeFile(workbook, `${filename}.xlsx`);
          this.dialogMes = false;
        } else {
          this.snackbarExport = true;
          this.text = "No hay registro de horarios para el mes seleccionado";
        }
      } else {
        this.snackbarExport = true;
        this.text = "Se debe seleccionar un mes para exportar";
      }
    },
    viewDay({ date }) {
      console.log(date);
      this.focus = date;
      this.type = "day";
    },
    getEventColor(event) {
      return event.color;
    },
    setToday() {
      this.focus = `${this.date}-07`;
      this.monthPicker = false;
    },
    prev() {
      this.$refs.calendar.prev();
    },
    next() {
      this.$refs.calendar.next();
    },
    showEvent({ nativeEvent, event }) {
      const open = () => {
        this.selectedEvent = event;
        this.selectedElement = nativeEvent.target;
        setTimeout(() => {
          this.selectedOpen = true;
        }, 10);
      };

      if (this.selectedOpen) {
        this.selectedOpen = false;
        setTimeout(open, 10);
      } else {
        open();
      }

      nativeEvent.stopPropagation();
    },
  },
};
</script>

<style lang="scss">
.btn-cas {
  background-color: #1f8d1b !important;
}

.v-current-time {
  height: 2px;
  background-color: #ffce00;
  position: absolute;
  left: -1px;
  right: 0;
  pointer-events: none;

  &.first::before {
    content: "";
    position: absolute;
    background-color: #ffce00;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    margin-top: -5px;
    margin-left: -6.5px;
  }
}
</style>
