<template>
  <v-form
    @submit.prevent="verificacionInfo()"
    lazy-validations
    style="padding-left: 40px;
    padding-right: 40px;"
  >
    <h1 class="font-weight-bold mb-4" style="padding-top: 20px">Iniciar sesin</h1>
    <v-text-field
      label="Correo electrnico"
      type="email"
      v-model="email"
      required
    ></v-text-field>

    <v-text-field
      label="Contrasea"
      type="password"
      v-model="password"
      required
    ></v-text-field>

    <v-btn 
      type="submit" 
      :loading="loading" 
      class="mt-7 mb-7" 
      block color="#0061aa" 
      style="color: white"
      @click="$store.commit('setSnackbarFirebase', false)"
      >
        Ingresar
    </v-btn>

    <router-link style="display: flex; justify-content: center" to="/reset">Olvidaste tu contrasea?</router-link>


    <v-snackbar v-model="snackbar" :timeout="timeout" color="#0061aa">
      {{text}}
      <template v-slot:action="{ attrs }">
        <v-btn
            color="white"
            text  
            v-bind="attrs"
            @click="snackbar = false"
        >
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>

    <v-snackbar v-if="errorHandling()" v-model="snackbarFirebase" :timeout="timeout" color="#0061aa">
      {{ text2 }}
      <template v-slot:action="{ attrs }">
        <v-btn
            color="white"
            text  
            v-bind="attrs"
            @click="$store.commit('setSnackbarFirebase', false)"
        >
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>


  </v-form>
</template>

<script>

import {mapActions, mapState, mapMutations} from 'vuex'
import { required, email } from 'vuelidate/lib/validators'
export default {
  name: 'LoginForm',
    data(){
      return{
        email: '',
        password: '',
        snackbar: false,
        text: '',
        timeout: 2000,
        text2: '',
      }
    },
    validations:{
      email:{required, email},
      password: {required}
    },
    methods:{
        ...mapActions(['ingresoUsuario']),
        ...mapMutations(['setSnackbarFirebase']),
        errorHandling(){
          if (this.error) {
            if(this.error.code === "auth/wrong-password" || this.error.code === "auth/user-not-found"){
              this.text2 = 'Usuario o contrasea ingresado es incorrecto'
              return true
            } else if (this.error.code === "auth/too-many-requests") {
              this.text2 = 'El acceso a la cuenta ha sido deshabilitado temporalmente. Restaure su contrasea o intente de nuevo mas tarde'
              return true
            }
          }
        },
        verificacionInfo(){
          if(!this.$v.email.required && !this.$v.password.required){
            this.text = 'Ambos campos deben ser ingresados'
            this.snackbar = true
            return
          } else if (!this.$v.email.required) {
            this.text = 'El correo electronico es necesario'
            this.snackbar = true
            return
          } else if (!this.$v.password.required){
            this.text = 'La contrasea es necesaria'
            this.snackbar = true
            return
          } else if (!this.$v.email.email){
            this.text = 'El email ingresado es invalido'
            this.snackbar = true
            return
          }
          this.ingresoUsuario({email: this.$v.email.$model, password: this.$v.password.$model})
        }
    },
    computed:{
      ...mapState(['error', 'loading', 'snackbarFirebase']),
    }
}

</script>

