<template v-bar>
  <v-row class="fill-height">
    <v-col>
      <v-sheet>
        <v-toolbar flat>
          <v-btn
            outlined
            class="mr-0"
            min-width="5%"
            color="grey darken-2"
            @click="monthPicker = true"
          >
            Mes
          </v-btn>
          <v-btn
            fab
            text
            small
            color="grey darken-2"
            @click="prev"
            v-if="!mobile"
          >
            <v-icon> mdi-chevron-left </v-icon>
          </v-btn>
          <v-btn
            fab
            text
            small
            color="grey darken-2"
            @click="next"
            v-if="!mobile"
          >
            <v-icon> mdi-chevron-right </v-icon>
          </v-btn>
          <v-toolbar-title
            class="d-none d-sm-block"
            v-if="$refs.calendar && !mobile"
          >
            {{ $refs.calendar.title }}
          </v-toolbar-title>
          <v-toolbar-title v-else-if="!mobile">
            {{ fechaActual }}
          </v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn
            id="btn-cas"
            class="btn-cas mr-2 clase-prueba"
            style="padding-left: 8px; padding-right: 8px"
            color="blue darken-3"
            @click="updateRange"
            dark
          >
            <span>Generar</span>
          </v-btn>
          <v-menu bottom right>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                outlined
                color="grey darken-2"
                v-bind="attrs"
                v-on="on"
                style="padding-left: 8px; padding-right: 8px"
              >
                <span>{{ typeToLabel[type] }}</span>
                <v-icon right> mdi-menu-down </v-icon>
              </v-btn>
            </template>
            <v-list>
              <v-list-item @click="type = 'day'">
                <v-list-item-title>Da</v-list-item-title>
              </v-list-item>
              <v-list-item v-if="!mobile" id="view-c" @click="type = 'week'">
                <v-list-item-title>Semana</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </v-toolbar>
        <v-toolbar
          flat
          v-if="mobile"
          style="display: flex; justify-content: center"
        >
          <v-btn fab text color="grey darken-2" @click="prev" class="mr-3">
            <v-icon> mdi-chevron-left </v-icon>
          </v-btn>
          <v-toolbar-title v-if="$refs.calendar">
            {{ $refs.calendar.title }}
          </v-toolbar-title>
          <v-toolbar-title v-else-if="mobile">
            {{ fechaActual }}
          </v-toolbar-title>
          <v-btn fab text color="grey darken-2" @click="next" class="ml-3">
            <v-icon> mdi-chevron-right </v-icon>
          </v-btn>
        </v-toolbar>
      </v-sheet>
      <v-sheet height="100%">
        <v-calendar
          id="cal"
          ref="calendar"
          :interval-format="intervalFormat"
          v-model="focus"
          color="primary"
          :events="eventos"
          :weekdays="weekday"
          :event-color="getEventColor"
          :type="type"
          @click:event="showEvent"
          @click:more="viewDay"
          @click:date="viewDay"
          event-overlap-mode="column"
        >
          <template v-slot:day-body="{ date, week }">
            <div
              class="v-current-time"
              :class="{ first: date === week[0].date }"
              :style="{ top: nowY }"
            ></div>
          </template>
          <template v-slot:event="{ event }">
            <div class="v-event-draggable pl-1 pt-1">
              <strong>{{ event.name }}</strong
              ><br />
              {{ formatEventTime(event.start) }} -
              {{ formatEventTime(event.end) }}
            </div>
          </template>
        </v-calendar>
        <v-menu
          v-model="selectedOpen"
          :close-on-content-click="false"
          :activator="selectedElement"
          offset-x
        >
          <v-card color="grey lighten-4" min-width="350px" flat>
            <v-toolbar :color="selectedEvent.color" dark>
              <v-toolbar-title v-html="selectedEvent.name"></v-toolbar-title>
              <v-spacer></v-spacer>
            </v-toolbar>
            <v-card-text>
              <span style="color: black" class="subheading font-weight-bold"
                >Cdula: </span>
              <span class="text--primary" v-html="selectedEvent.cedula"></span>
              <br />
              <span style="color: black" class="subheading font-weight-bold"
                >rea o servicio: </span
              >
              <span class="text--primary" v-html="selectedEvent.details1"></span>
              <br />
              <span style="color: black" class="subheading font-weight-bold"
                >Centro de costo: </span>
              <span class="text--primary" v-html="selectedEvent.details2"></span>
              <br />
              <span style="color: black" class="subheading font-weight-bold"
                >Tipo de contrato: </span>
              <span class="text--primary" v-html="selectedEvent.tipocontrato"></span>
              <br />
              <span style="color: black" class="subheading font-weight-bold"
                >Cargo: </span>
              <span class="text--primary" v-html="selectedEvent.cargo"></span>
            </v-card-text>
            <v-card-actions>
              <v-btn text color="secondary" @click="selectedOpen = false">
                Cerrar
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-menu>
      </v-sheet>

      <!-- Filtrado de funcionario y Ventana de confirmacion -->
      <Dialog v-bind:arrayFuncionarios="funcionarios"></Dialog>

      <v-dialog v-model="dialogGen" hide-overlay persistent width="300">
        <v-card color="blue darken-4" dark>
          <v-card-text>
            Generando...
            <v-progress-linear
              rounded
              indeterminate
              color="white"
              class="mb-0"
            ></v-progress-linear>
          </v-card-text>
        </v-card>
      </v-dialog>

      <v-dialog
        v-if="dialogMes"
        v-model="dialogMes"
        scrollable
        max-width="300px"
      >
        <v-card>
          <v-card-title>Seleccione el mes</v-card-title>
          <v-divider></v-divider>
          <v-card-text>
            <v-radio-group v-model="opcion" column>
              <v-radio
                v-for="mes in meses"
                :key="mes.id"
                :value="mes.id"
                :label="mes.name"
              ></v-radio>
            </v-radio-group>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-actions>
            <v-btn color="blue darken-1" text @click="dialogMes = false">
              Cerrar
            </v-btn>
            <v-btn color="blue darken-1" text @click="generateShifts(opcion)">
              Generar turnos
            </v-btn>
          </v-card-actions>

          <v-snackbar v-model="snackbar" :timeout="timeout" color="#0061aa">
            {{ text }}
            <template v-slot:action="{ attrs }">
              <v-btn
                color="white"
                text
                v-bind="attrs"
                @click="snackbar = false"
              >
                Cerrar
              </v-btn>
            </template>
          </v-snackbar>
        </v-card>
      </v-dialog>

      <v-dialog v-model="monthPicker" v-if="monthPicker" max-width="290px">
        <v-card>
          <v-date-picker
            v-model="date"
            type="month"
            min="2021-01"
            max="2021-12"
          ></v-date-picker>
          <v-divider></v-divider>
          <v-card-actions>
            <v-btn color="blue darken-1" text @click="monthPicker = false">
              Cerrar
            </v-btn>
            <v-btn color="blue darken-1" text @click="setToday()">
              Aceptar
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-snackbar v-model="snackbarWarning" :timeout="timeout" color="red">
        ERROR: El mnimo nmero de funcionarios para generar los turnos son 10
        <template v-slot:action="{ attrs }">
          <v-btn
            color="white"
            text
            v-bind="attrs"
            @click="snackbarWarning = false"
          >
            Cerrar
          </v-btn>
        </template>
      </v-snackbar>
    </v-col>
  </v-row>
</template>

<script>
import Dialog from "../components/Dialog.vue";
import { mapActions, mapState, mapMutations } from "vuex";

export default {
  components: {
    Dialog,
  },
  props: {
    title: {
      type: String,
    },
    area: {
      type: String,
    },
  },
  data() {
    return {
      snackbarWarning: false,
      fechaActual: "",
      monthPicker: false,
      date: "",
      headers: [
        {
          text: "Nombre funcionario",
          align: "start",
          sortable: false,
          value: "name",
        },
      ],
      meses: [
        { id: "01", name: "Enero" },
        { id: "02", name: "Febrero" },
        { id: "03", name: "Marzo" },
        { id: "04", name: "Abril" },
        { id: "05", name: "Mayo" },
        { id: "06", name: "Junio" },
        { id: "07", name: "Julio" },
        { id: "08", name: "Agosto" },
        { id: "09", name: "Septiembre" },
        { id: "10", name: "Octubre" },
      ],
      snackbar: false,
      text: "",
      timeout: 2000,
      opcion: "",
      dialogMes: false,
      dialog: false,
      dialogItem: false,
      dialogGen: false,
      fechaInicio: "",
      fechaFin: "",
      ready: false,
      dataToExport: [],
      focus: "",
      type: "week",
      typeToLabel: {
        month: "Month",
        week: "Semana",
        day: "Da",
        "4day": "4 Days",
      },
      selectedEvent: {},
      selectedElement: null,
      selectedOpen: false,
      weekday: [1, 2, 3, 4, 5, 6, 0],
      info: [],
      events: [],

      colors: [
        "blue",
        "indigo",
        "deep-purple",
        "cyan",
        "green",
        "orange",
        "grey darken-1",
      ],
      names: [
        "Meeting",
        "Holiday",
        "PTO",
        "Travel",
        "Event",
        "Birthday",
        "Conference",
        "Party",
      ],
    };
  },
  computed: {
    ...mapState(["eventos", "funcionarios", "backup_eventos", "helper"]),
    cal() {
      return this.ready ? this.$refs.calendar : null;
    },
    nowY() {
      return this.cal ? this.cal.timeToY(this.cal.times.now) + "px" : "-10px";
    },
    mobile() {
      if (this.$vuetify.breakpoint.xs) {
        this.type = "day";
        return true;
      } else {
        this.type = "week";
        return false;
      }
    },
  },
  mounted() {
    this.ready = true;
    this.scrollToTime();
    this.updateTime();
    this.$refs.calendar.checkChange();
    this.getDate();
  },
  methods: {
    ...mapActions([
      "updateEventos",
      "updateFuncionarios",
      "updateBackupEventos",
    ]),
    ...mapMutations([
      "setEventos",
      "setFuncionarios",
      "setBackupEventos",
      "setHelper",
    ]),
    intervalFormat(interval) {
      return interval.time;
    },
    formatEventTime(date) {
      return new Date(date).toLocaleTimeString("en-US", {
        hour: "2-digit",
        minute: "2-digit",
        hour12: false,
      });
    },
    updateShifts() {
      this.dialog = true;
    },
    editItem(data) {
      this.dialogItem = true;
      this.fechaInicio = data.start;
      this.fechaFin = data.end;
    },
    getCurrentTime() {
      return this.cal
        ? this.cal.times.now.hour * 60 + this.cal.times.now.minute
        : 0;
    },
    scrollToTime() {
      const time = this.getCurrentTime();
      const first = Math.max(0, time - (time % 30) - 30);

      this.cal.scrollToTime(first);
    },
    updateTime() {
      setInterval(() => this.cal.updateTimes(), 60 * 1000);
    },
    viewDay({ date }) {
      this.focus = date;
      this.type = "day";
    },
    getEventColor(event) {
      return event.color;
    },
    setToday() {
      this.focus = `${this.date}-07`;
      this.monthPicker = false;
    },
    prev() {
      this.$refs.calendar.prev();
    },
    next() {
      this.$refs.calendar.next();
    },
    showEvent({ nativeEvent, event }) {
      const open = () => {
        this.selectedEvent = event;
        this.selectedElement = nativeEvent.target;
        setTimeout(() => {
          this.selectedOpen = true;
        }, 10);
      };

      if (this.selectedOpen) {
        this.selectedOpen = false;
        setTimeout(open, 10);
      } else {
        open();
      }

      nativeEvent.stopPropagation();
    },
    updateRange() {
      this.dialogMes = true;
    },
    generateShifts() {
      if (this.opcion == "") {
        this.snackbar = true;
        this.text = "Debe ingresar un mes para generar los turnos";
      } else {
        console.log(this.opcion);
        this.axios
          .get(
            "/verificar",
            { params: { mes: this.opcion, area: this.area } },
            { timeout: 600000 }
          )
          .then((response) => {
            console.log(response.data[0].booleano);
            if (response.data[0].booleano == 0) {
              this.dialogMes = false;
              this.turnos();
            } else {
              this.snackbar = true;
              this.text =
                "Los turnos para el mes seleccionado ya fueron generados. Por favor, seleccione otro mes";
            }
          })
          .catch((e) => {
            console.log(e.response);
          });
      }
    },
    turnos() {
      this.dialogGen = true;
      this.axios
        .get(this.title, { params: { mes: this.opcion } })
        .then((response) => {
          this.dialogGen = false;
          let res = response;
          console.log(response.data);
          if (res.data[0].length > 1 && res.data[1].length > 1) {
            this.$store.dispatch("updateEventos", res.data[0]);
            this.$store.dispatch("updateBackupEventos", res.data[0]);
            this.$store.dispatch("updateFuncionarios", res.data[1]);
            this.$store.commit("setHelper", true);
            this.focus = `2021-${this.opcion}-07`;
          } else {
            this.snackbarWarning = true;
          }
        })
        .catch((e) => {
          console.log(e.response);
        });
    },
    getDate() {
      const date = new Date();
      const fecha = date.toLocaleString(undefined, {
        month: "long",
        year: "numeric",
      });
      this.fechaActual = fecha;
    },
  },
};
</script>

<style lang="scss">
.btn-cas {
  background-color: #1f8d1b !important;
}

.v-current-time {
  height: 2px;
  background-color: #ffce00;
  position: absolute;
  left: -1px;
  right: 0;
  pointer-events: none;

  &.first::before {
    content: "";
    position: absolute;
    background-color: #ffce00;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    margin-top: -5px;
    margin-left: -6.5px;
  }
}
</style>
