<template>
  <v-form
    lazy-validations
    style="padding-left: 40px;
    padding-right: 40px;"
    @submit.prevent="areaNueva()"
  >
    <h1
      class="hidden-sm-and-down font-weight-bold mb-4"
      style="padding-top: 20px"
    >
      Ingresa los datos
    </h1>
    <h1
      class="hidden-md-and-up font-weight-bold mb-4"
      style="padding-top: 20px"
    >
      Agregar area o servicio
    </h1>

    <v-text-field
      label="Nombre del rea o servicio"
      v-model="nombre"
      required
    ></v-text-field>

    <v-btn
      type="submit"
      class="mt-7 mb-7"
      block
      color="#008dca"
      style="color: white"
      :disabled="disabled"
    >
      Crear
    </v-btn>

    <v-snackbar v-model="snackbar" :timeout="timeout" color="#0061aa">
      {{ text }}
      <template v-slot:action="{ attrs }">
        <v-btn color="white" text v-bind="attrs" @click="snackbar = false">
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>
  </v-form>
</template>

<script>
import { required, alpha, helpers } from "vuelidate/lib/validators";
export default {
  data() {
    return {
      nombre: "",
      snackbar: false,
      text: "",
      timeout: 2000,
    };
  },
  validations: {
    nombre: { required },
  },
  methods: {
    areaNueva() {
      if (!this.$v.nombre.required) {
        this.text = "Debe ingresar el nombre de la nueva area o servicio";
        this.snackbar = true;
        return;
      } else {
        this.axios
          .post("/areanueva", {
            nombre: this.$v.nombre.$model.toUpperCase(),
          })
          .then((response) => {
            // Respuesta del servidor
            this.text =
              "El area o servicio se ha creado correctamente";
            this.snackbar = true;
            this.nombre = ""
          })
          .catch((e) => {
            console.log(e);
          });
      }
    },
  },
};
</script>

<style></style>
