<template>
  <v-form
    lazy-validations
    style="padding-left: 40px;
    padding-right: 40px;"
    @submit.prevent="verificacionInfo2()"
  >
    <h1 class="font-weight-bold mb-4" style="padding-top: 20px">Recupera tu contrasea</h1>
    <v-text-field
      label="Correo electrnico"
      type="email"
      v-model="email"
      required
    ></v-text-field>

    <v-btn 
      type="submit" 
      class="mt-7 mb-7" 
      block color="#0061aa" 
      style="color: white" 
      :loading="loading" 
      @click="$store.commit('setSnackbarFirebase', false)"
      >
        Enviar correo
    </v-btn>
    <router-link style="display: flex; justify-content: center" to="/">Volver a inicio</router-link>

    <!-- Snackbar para verificar el campo de entrada -->
    <v-snackbar v-model="snackbar" :timeout="timeout" color="#0061aa">
      {{text}}
      <template v-slot:action="{ attrs }">
        <v-btn
            color="white"
            text  
            v-bind="attrs"
            @click="snackbar = false"
        >
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>

    <!-- Snackbars para las respuestas de Firebase -->
    <v-snackbar v-if="error" v-model="snackbarFirebase" :timeout="timeout" color="#0061aa">
      El correo electrnico ingresado no est registrado
      <template v-slot:action="{ attrs }">
        <v-btn
            color="white"
            text  
            v-bind="attrs"
            @click="$store.commit('setSnackbarFirebase', false)"
        >
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>
    <v-snackbar v-else v-model="snackbarFirebase" :timeout="timeout" color="#0061aa">
      El mensaje para el cambio de contrasea se ha enviado correctamente
      <template v-slot:action="{ attrs }">
        <v-btn
            color="blue"
            text  
            v-bind="attrs"
            @click="$store.commit('setSnackbarFirebase', false)"
        >
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>


  </v-form>
</template>

<script>
import {mapActions, mapState, mapMutations} from 'vuex'
import { required, email } from 'vuelidate/lib/validators'
export default {
    data(){
        return{
          text:'',
          email:'',
          snackbar: false,
          timeout: 2000,
        }
    },
    methods:{
        ...mapActions(['restablecerContrasea']),
        ...mapMutations(['setSnackbarFirebase']),
        verificacionInfo2(){
          if(!this.$v.email.required){
            this.text = 'El campo de correo electrnico es necesario'
            this.snackbar = true
            return
          } else if (!this.$v.email.email){
            this.text = 'El email ingresado es invlido'
            this.snackbar = true
            return
          }
          this.restablecerContrasea(this.$v.email.$model)
        }
    },
    computed:{
      ...mapState(['loading', 'error', 'snackbarFirebase'])
    },
    validations:{
      email:{required, email}
    },
}
</script>

