<template>
  <v-form
    lazy-validations
    style="padding-left: 40px;
    padding-right: 40px;"
    @submit.prevent="verificacionInfo()"
  >
    <h1 class="font-weight-bold mb-4" style="padding-top: 20px">
      Registro de coordinadores
    </h1>
    <v-text-field
      label="Correo electrnico"
      type="email"
      v-model="email"
      required
    ></v-text-field>

    <v-text-field
      label="Contrasea"
      type="password"
      v-model="password"
      required
    ></v-text-field>

    <v-text-field
      label="Repetir contrasea"
      type="password"
      v-model="confirmPass"
      required
    ></v-text-field>

    <v-btn
      type="submit"
      class="mt-7 mb-7"
      block
      color="#0061aa"
      style="color: white"
      :loading="loading"
    >
      Registrar
    </v-btn>

    <v-snackbar v-model="snackbar" :timeout="timeout" color="#0061aa">
      {{ text }}
      <template v-slot:action="{ attrs }">
        <v-btn color="white" text v-bind="attrs" @click="snackbar = false">
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>

    <v-snackbar
      v-if="errorHandling()"
      v-model="snackbarFirebase"
      :timeout="timeout"
      color="#0061aa"
    >
      {{ text2 }}
      <template v-slot:action="{ attrs }">
        <v-btn
          color="white"
          text
          v-bind="attrs"
          @click="$store.commit('setSnackbarFirebase', false)"
        >
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>

    <v-snackbar v-model="success" :timeout="timeout" color="#0061aa">
      El coordinador ha sido registrado satisfactoriamente
      <template v-slot:action="{ attrs }">
        <v-btn color="white" text v-bind="attrs" @click="$store.commit('setSuccess', null)">
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>

  </v-form>
</template>

<script>
import { mapActions, mapState, mapMutations } from "vuex";
import { required, email, minLength, sameAs } from "vuelidate/lib/validators";
export default {
  data() {
    return {
      email: "",
      password: "",
      confirmPass: "",
      snackbar: false,
      text: "",
      timeout: 2000,
    };
  },
  validations: {
    email: { required, email },
    password: { required, minLength: minLength(6) },
    confirmPass: { required, sameAs: sameAs("password") },
  },
  methods: {
    ...mapActions(["crearUsuario"]),
    ...mapMutations(["setSnackbarFirebase", "setSuccess"]),
    verificacionInfo() {
      if (
        !this.$v.email.required &&
        !this.$v.password.required &&
        !this.$v.confirmPass.required
      ) {
        this.text = "Todos los campos deben ser ingresados";
        this.snackbar = true;
        return;
      } else if (!this.$v.email.required) {
        this.text = "El correo electronico es necesario";
        this.snackbar = true;
        return;
      } else if (!this.$v.password.required) {
        this.text = "La contrasea es necesaria";
        this.snackbar = true;
        return;
      } else if (!this.$v.confirmPass.required) {
        this.text = "Debe repetir la contrasea";
        this.snackbar = true;
        return;
      } else if (!this.$v.email.email) {
        this.text = "El email ingresado es invalido";
        this.snackbar = true;
        return;
      } else if (!this.$v.password.minLength) {
        this.text = "La contrasea debe tener un mnimo de 6 carcteres";
        this.snackbar = true;
        return;
      } else if (!this.$v.confirmPass.sameAs) {
        this.text = "Las contraseas no concuerdan. Intente otra vez";
        this.snackbar = true;
        return;
      }
      this.crearUsuario({
        email: this.$v.email.$model,
        password: this.$v.password.$model,
      });
    },
    errorHandling() {
      if (this.error) {
        if (this.error.code === "auth/email-already-in-use") {
          this.text2 =
            "El correo electrnico ingresado ya se encuentra registrado";
          return true;
        }
      }
    },
  },
  computed: {
    ...mapState(["error", "loading", "snackbarFirebase", "success"]),
  },
};
</script>

<style></style>
