<template>
  <v-form
    lazy-validations
    style="padding-left: 40px;
    padding-right: 40px;"
    @submit.prevent="modifyUser()"
  >
    <h1
      class="hidden-sm-and-down font-weight-bold mb-4"
      style="padding-top: 20px"
    >
      Ingresa los datos
    </h1>
    <h1
      class="hidden-md-and-up font-weight-bold mb-4"
      style="padding-top: 20px"
    >
      Modificar funcionarios
    </h1>

    <v-select
      v-model="area"
      :items="areas"
      item-text="Area"
      item-value="idAreas"
      label="Area o servicio"
      required
    ></v-select>

    <v-btn
      class="mt-7 mb-7"
      block
      color="#0061aa"
      style="color: white"
      @click="verificacionInfo()"
    >
      Buscar
    </v-btn>

    <v-divider></v-divider>

    <v-select
      :disabled="disabled_input"
      v-model="funcionario"
      :items="funcionarios"
      item-text="Funcionario"
      item-value="idFuncionarios"
      label="Funcionario"
      required
    ></v-select>

    <v-select
      :disabled="disabled_input"
      v-on:change="changeOptions(criterio)"
      v-model="criterio"
      :items="criterios"
      label="Criterio de cambio"
      required
    ></v-select>

    <v-select
      :disabled="disabled_input"
      v-model="opcion"
      :items="opciones"
      item-text="Area"
      item-value="idAreas"
      :label="text_opciones"
      required
    ></v-select>

    <v-select
      v-if="cond"
      v-model="opcion2"
      :items="opciones2"
      label="Nuevo centro de costo"
      required
    ></v-select>

    <v-btn
      type="submit"
      class="mt-7 mb-7"
      block
      color="#008dca"
      style="color: white"
      :disabled="disabled"
    >
      Enviar cambios
    </v-btn>

    <v-snackbar v-model="snackbar" :timeout="timeout" color="#0061aa">
      {{ text }}
      <template v-slot:action="{ attrs }">
        <v-btn color="white" text v-bind="attrs" @click="snackbar = false">
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>
  </v-form>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { required } from "vuelidate/lib/validators";
export default {
  data() {
    return {
      disabled_input: true,
      text_opciones: "Opciones",
      cond: false,
      disabled: true,
      opciones: [],
      opciones2: [],
      opcion: "",
      opcion2: "",
      criterio: "",
      funcionarios: [],
      nombre: "",
      apellido: "",
      area: "",
      funcionario: "",
      centro: "",
      snackbar: false,
      text: "",
      timeout: 2000,
      criterios: [
        "Area o servicio",
        "Centro de costo",
        "Estado del funcionario",
        "Tipo de contrato",
        "Area o servicio y Centro de costo",
      ],
      centros: [
        "HA001",
        "HA002",
        "HA003",
        "HA004",
        "HA005",
        "HA006",
        "HA010",
        "HA013",
        "HA018",
        "HA021",
        "HA023",
        "HM102",
      ],
      areas: [],
      estados: [
        "Habilitar"
      ],
      contratos: [
        "Tiempo completo",
        "Medio tiempo"
      ]
    };
  },
  validations: {
    opcion: { required },
    funcionario: { required },
    criterio: { required },
    opcion2: { required },
    area: { required },
  },
  mounted(){
    this.areasQuery()
  },
  methods: {
    ...mapActions(["crearUsuario"]),
    areasQuery() {
      this.axios
        .get("/areas")
        .then((response) => {
          // Respuesta del servidor
          this.areas = response.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    modifyUser() {
      if (this.cond) {
        if (
          !this.$v.funcionario.required ||
          !this.$v.criterio.required ||
          !this.$v.opcion.required ||
          !this.$v.opcion2.required
        ) {
          this.text = "Debe ingresar todos los campos";
          this.snackbar = true;
          return;
        } else {
          this.axios
            .put("/modificar", {
              data: {
                nombre: this.$v.funcionario.$model,
                opcion: this.$v.opcion.$model,
                opcion2: this.$v.opcion2.$model,
              },
            })
            .then((response) => {
              // Respuesta del servidor
              this.text = "El funcionario ha sido modificado correctamente";
              this.snackbar = true;
              this.disabled = true;
              this.disabled_input = true;
              this.cond = false
              this.opcion = ""
              this.opcion2 = ""
              this.criterio = ""
              this.nombre = ""
              this.apellido = ""
              this.funcionario = ""
              this.centro = ""
            })
            .catch((e) => {
              console.log(e);
            });
        }
      } else {
        if (
          !this.$v.funcionario.required ||
          !this.$v.criterio.required ||
          !this.$v.opcion.required
        ) {
          this.text = "Debe ingresar todos los campos";
          this.snackbar = true;
          return;
        } else {
          this.axios
            .put("/modificar", {
              data: {
                nombre: this.$v.funcionario.$model,
                opcion: {
                  criterio: this.$v.criterio.$model,
                  valor: this.$v.opcion.$model,
                },
              },
            })
            .then((response) => {
              // Respuesta del servidor
              this.text = "El funcionario ha sido modificado correctamente";
              this.snackbar = true;
              this.disabled = true;
              this.disabled_input = true;
              this.cond = false
              this.opcion = ""
              this.opcion2 = ""
              this.criterio = ""
              this.nombre = ""
              this.apellido = ""
              this.funcionario = ""
              this.centro = ""
            })
            .catch((e) => {
              console.log(e);
            });
        }
      }
    },
    verificacionInfo() {
      if (!this.$v.area.required) {
        this.text = "El campo de rea o servicio es necesario";
        this.snackbar = true;
        return;
      } else {
        this.disabled_input = false;
        this.axios
          .get("/filtrartodos", { params: { area: this.$v.area.$model } })
          .then((res) => {
            let funcionarios = [];
            res.data.forEach((element) => {
              funcionarios.push({
                idFuncionarios: element.idFuncionarios,
                Funcionario: `(CC:${element.Cedula}) - ${element.Nombre} ${element.Apellido}`,
              });
            });
            this.funcionarios = funcionarios;
            this.disabled = false;
          })
          .catch((error) => {});
      }
    },
    changeOptions(criterio) {
      if (criterio == "Area o servicio") {
        this.opciones = this.areas;
        this.cond = false;
        this.text_opciones = "Opciones";
      } else if (criterio == "Centro de costo") {
        this.opciones = this.centros;
        this.cond = false;
        this.text_opciones = "Opciones";
      } else if (criterio == "Estado del funcionario") {
        this.opciones = this.estados;
        this.cond = false;
        this.text_opciones = "Opciones";
      } else if (criterio == "Tipo de contrato") {
        this.opciones = this.contratos;
        this.cond = false;
        this.text_opciones = "Opciones";
      } else {
        this.cond = true;
        this.opciones = this.areas;
        this.opciones2 = this.centros;
        this.text_opciones = "Nueva Area o servicio";
      }
    },
  },
};
</script>

<style></style>
