<template>
  <v-form
    lazy-validations
    style="padding-left: 40px;
    padding-right: 40px;"
    @submit.prevent="deleteUser()"
  >
    <h1
      class="hidden-sm-and-down font-weight-bold mb-4"
      style="padding-top: 20px"
    >
      Ingresa los datos
    </h1>
    <h1
      class="hidden-md-and-up font-weight-bold mb-4"
      style="padding-top: 20px"
    >
      Deshabilitar funcionarios
    </h1>

    <v-select
      v-model="area"
      :items="areas"
      item-text="Area"
      item-value="idAreas"
      label="Area o servicio"
      required
    ></v-select>

    <v-btn
      class="mt-7 mb-7"
      block
      color="#0061aa"
      style="color: white"
      @click="verificacionInfo()"
    >
      Buscar
    </v-btn>

    <v-divider></v-divider>

    <v-select
      :disabled="disabled"
      v-model="funcionario"
      :items="funcionarios"
      item-text="Funcionario"
      item-value="idFuncionarios"
      label="Funcionario"
      required
    ></v-select>

    <v-btn
      type="submit"
      class="mt-7 mb-7"
      block
      color="error"
      style="color: white"
      :disabled="disabled"
    >
      Deshabilitar
    </v-btn>

    <v-snackbar v-model="snackbar" :timeout="timeout" color="#0061aa">
      {{ text }}
      <template v-slot:action="{ attrs }">
        <v-btn color="white" text v-bind="attrs" @click="snackbar = false">
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>
  </v-form>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { required } from "vuelidate/lib/validators";
export default {
  data() {
    return {
      disabled: true,
      funcionarios: [],
      nombre: "",
      apellido: "",
      area: "",
      funcionario: "",
      centro: "",
      snackbar: false,
      text: "",
      timeout: 2000,
      areas: [],
    };
  },
  validations: {
    area: { required },
    funcionario: { required },
  },
  mounted() {
    this.areasQuery();
  },
  methods: {
    ...mapActions(["crearUsuario"]),
    areasQuery() {
      this.axios
        .get("/areas")
        .then((response) => {
          // Respuesta del servidor
          this.areas = response.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    deleteUser() {
      if (!this.$v.funcionario.required) {
        this.text = "Debe elegir un funcionario a eliminar";
        this.snackbar = true;
        return;
      } else {
        this.axios
          .put("/deshabilitar", {
            data: {
              idFuncionario: this.$v.funcionario.$model,
            },
          })
          .then((response) => {
            // Respuesta del servidor
            this.text = "El funcionario ha sido deshabilitado correctamente";
            this.snackbar = true;
            this.disabled = true;
            this.funcionario = ""
          })
          .catch((e) => {
            console.log(e);
          });
      }
    },
    verificacionInfo() {
      if (!this.$v.area.required) {
        this.text = "El campo de area o servicio es necesario";
        this.snackbar = true;
        return;
      } else {
        this.axios
          .get("/filtrar", { params: { area: this.$v.area.$model } })
          .then((res) => {
            let funcionarios = [];
            res.data.forEach((element) => {
              funcionarios.push({
                idFuncionarios: element.idFuncionarios,
                Funcionario: `(CC: ${element.Cedula}) ${element.Nombre} ${element.Apellido}`,
              });
            });
            this.funcionarios = funcionarios;
            this.disabled = false;
          })
          .catch((error) => {});
      }
    },
  },
};
</script>

<style></style>
