<template>
  <v-row justify="center">
    <v-dialog v-if="dialog" v-model="dialog" scrollable max-width="300px">
      <v-card>
        <v-card-title>Seleccione el funcionario</v-card-title>
        <v-divider></v-divider>
        <v-card-text style="height: 300px;">
          <v-radio-group v-model="opcion" column>
            <v-radio label="Todos" value="Todos"> </v-radio>
            <v-radio
              v-for="funcionario in funcionarios"
              :key="funcionario.name"
              :value="funcionario.name"
              :label="funcionario.name"
            ></v-radio>
          </v-radio-group>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn color="blue darken-1" text @click="dialog = false">
            Cerrar
          </v-btn>
          <v-btn color="blue darken-1" text @click="filtrarCalendario(opcion)">
            Guardar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-speed-dial v-model="fab" fixed right bottom>
      <template v-slot:activator>
        <v-btn v-model="fab" color="blue darken-2" dark fab>
          <v-icon v-if="fab">
            mdi-close
          </v-icon>
          <v-icon v-else>
            mdi-cog
          </v-icon>
        </v-btn>
      </template>
      <v-btn fab dark small color="green" @click="dialog = true">
        <v-icon>mdi-filter</v-icon>
      </v-btn>
      <v-btn fab dark small color="indigo" @click="dialogVentana = true">
        <v-icon>mdi-update</v-icon>
      </v-btn>
    </v-speed-dial>
    <v-dialog
      v-model="dialogVentana"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar dark color="primary">
          <v-btn
            icon
            dark
            @click="
              dialogVentana = false;
              actualizarCalendario();
            "
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Ventana de modificacin</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn
              dark
              text
              @click="
                dialog = false;
                actualizarTurnos();
              "
            >
              Actualizar
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-list three-line subheader>
          <v-data-table
            :headers="headers"
            :items="arrayFuncionarios"
            :single-expand="true"
            :expanded.sync="expanded"
            item-key="id"
            show-expand
            class="elevation-1"
          >
            <template v-slot:expanded-item="{ headers, item }">
              <td :colspan="headers.length" class="py-5">
                <v-row>
                  <v-col cols="3" style="text-align: center">
                    <p class="font-weight-bold">
                      NOMBRE
                    </p>
                  </v-col>
                  <v-col cols="3" style="text-align: center">
                    <p class="font-weight-bold">
                      FECHA INICIO
                    </p>
                  </v-col>
                  <v-col cols="3" style="text-align: center">
                    <p class="font-weight-bold">
                      FECHA FIN
                    </p>
                  </v-col>
                  <v-col cols="3" style="text-align: center">
                    <p class="font-weight-bold">
                      ACCIN
                    </p>
                  </v-col>
                </v-row>
                <v-row v-for="(data, i) in item.horarios" :key="i">
                  <v-col cols="3" style="text-align: center">
                    {{ data.name }}
                  </v-col>
                  <v-col cols="3" style="text-align: center">
                    {{ data.start }}
                  </v-col>
                  <v-col cols="3" style="text-align: center">
                    {{ data.end }}
                  </v-col>
                  <v-col cols="3" style="text-align: center">
                    <v-icon
                      small
                      class="mr-3"
                      @click="editItem(item.id, i, data)"
                    >
                      mdi-pencil
                    </v-icon>
                    <v-icon small class="ml-3" @click="deleteItem(item.id, i, data)">
                      mdi-delete
                    </v-icon>
                  </v-col>
                </v-row>
              </td>
            </template>
          </v-data-table>
        </v-list>
      </v-card>
      <v-dialog v-model="dialogItem" max-width="500px">
        <v-card>
          <v-card-title>
            <span class="headline">Editar turnos</span>
          </v-card-title>

          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="6">
                  <v-menu
                    v-model="menu2"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    transition="scale-transition"
                    offset-y
                    min-width="auto"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="diaInicio"
                        label="Fecha inicio del turno"
                        prepend-icon="mdi-calendar"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-date-picker
                      v-model="diaInicio"
                      @input="menu2 = false"
                      min="2021-01"
                      max="2021-12"
                    ></v-date-picker>
                  </v-menu>
                </v-col>
                <v-col cols="6">
                  <v-radio-group v-model="radioGroup">
                    <v-radio label="Corrido" value="1"></v-radio>
                    <v-radio label="Noche" value="2"></v-radio>
                    <v-radio label="JC" value="3"></v-radio>
                    <v-radio label="Maana" value="4"></v-radio>
                    <v-radio label="Incapacidad" value="5"></v-radio>
                  </v-radio-group>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="dialogItem = false; this.snackbarChanges1 = false;">
              Cancelar
            </v-btn>
            <v-btn color="blue darken-1" text @click="saveChanges()">
              Guardar
            </v-btn>
          </v-card-actions>
        </v-card>
        <v-snackbar v-model="snackbarChanges1" :timeout="timeout" color="red">
          ERROR: Los turnos pasados registrados no pueden ser
          modificados
          <template v-slot:action="{ attrs }">
            <v-btn
              color="white"
              text
              v-bind="attrs"
              @click="snackbarChanges1 = false"
            >
              Cerrar
            </v-btn>
          </template>
        </v-snackbar>
      </v-dialog>

      <v-dialog v-model="dialogDelete" max-width="500px">
        <v-card>
          <v-card-title>
            <span class="headline"
              >Ests seguro que deseas borrar el turno seleccionado?</span
            >
          </v-card-title>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="dialogDelete = false; this.snackbarChanges2 = false;">
              Cancelar
            </v-btn>
            <v-btn color="blue darken-1" text @click="borrarTurno()">
              Aceptar
            </v-btn>
          </v-card-actions>
        </v-card>
        <v-snackbar v-model="snackbarChanges2" :timeout="timeout" color="red">
          ERROR: Los turnos pasados registrados no pueden ser eliminados
          <template v-slot:action="{ attrs }">
            <v-btn
              color="white"
              text
              v-bind="attrs"
              @click="snackbarChanges2 = false"
            >
              Cerrar
            </v-btn>
          </template>
        </v-snackbar>
      </v-dialog>
    </v-dialog>

    <v-snackbar v-model="snackbar" :timeout="timeout" color="#0061aa">
      Los turnos han sido actualizados satisfactoriamente. Para exportar los nuevos cambios,
      recargue la pgina
      <template v-slot:action="{ attrs }">
        <v-btn color="white" text v-bind="attrs" @click="snackbar = false">
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>
  </v-row>
</template>

<script>
import { mapActions, mapState, mapMutations } from "vuex";
export default {
  props: {
    arrayFuncionarios: {
      type: Array,
    },
  },
  data() {
    return {
      snackbarChanges1: false,
      snackbarChanges2: false,
      dialogDelete: false,
      turnosActualizados: [],
      turnosBorrados: [],
      snackbarEditar: false,
      snackbar: false,
      timeout: 3000,
      idFuncionario: "",
      idHorario: "",
      newHoraInicio: [],
      newHoraFin: [],
      radioGroup: "",
      menu2: false,
      headers: [
        {
          text: "Nombre funcionario",
          align: "start",
          sortable: false,
          value: "name",
        },
      ],
      dialog: false,
      dialogVentana: false,
      dialogItem: false,
      fechaInicio: "",
      fechaFin: "",
      diaInicio: "",
      opcion: "",
      fab: false,
    };
  },
  methods: {
    ...mapActions([
      "updateEventos",
      "updateFuncionarios",
      "updateBackupEventos",
    ]),
    ...mapMutations(["setEventos", "setFuncionarios", "setBackupEventos"]),
    showData() {
      console.log(this.data);
    },
    filtrarCalendario(opcion) {
      if (opcion == "Todos") {
        this.$store.dispatch("updateEventos", this.backup_eventos);
      } else {
        const found = this.funcionarios.find(
          (element) => element.name == opcion
        );
        this.$store.dispatch("updateEventos", found.horarios);
      }
      this.dialog = false;
    },
    deleteItem(idFuncionario, idHorario, data) {
      this.idFuncionario = idFuncionario;
      this.idHorario = idHorario;
      this.dialogDelete = true;
      let fecha_inicio = data.start.split(" ");
      this.diaInicio = fecha_inicio[0];
      this.fechaInicio = data.start;
      this.fechaFin = data.end;
    },
    borrarTurno() {
      let mes = new Date();
      let dia_actual = mes.getDate();
      let mes_actual = mes.getMonth() + 1;
      let diaInicio = JSON.parse(JSON.stringify(this.diaInicio));
      let fecha_edit = diaInicio.split("-");
      let mes_edit = fecha_edit[1]
      let dia_edit = fecha_edit[2]
      mes_edit = parseInt(mes_edit, 10);
      dia_edit = parseInt(dia_edit, 10);
      if (mes_edit >= mes_actual && dia_edit > dia_actual) {
        let idFunc = this.findIndexInData(
          this.arrayFuncionarios,
          "id",
          this.idFuncionario
        );
        this.turnosBorrados.push(
          this.arrayFuncionarios[idFunc].horarios[this.idHorario]
        );
        this.arrayFuncionarios[idFunc].horarios.splice(this.idHorario, 1);
        this.dialogDelete = false;
      } else {
        this.snackbarChanges2 = true;
      }
    },
    updateShifts() {
      this.dialogVentana = true;
    },
    editItem(idFuncionario, idHorario, data) {
      this.idFuncionario = idFuncionario;
      this.idHorario = idHorario;
      this.dialogItem = true;
      let fecha_inicio = data.start.split(" ");
      this.diaInicio = fecha_inicio[0];
      this.fechaInicio = data.start;
      this.fechaFin = data.end;
    },
    saveChanges() {
      let mes = new Date();
      let dia_actual = mes.getDate()
      let mes_actual = mes.getMonth() + 1;
      let diaInicio = JSON.parse(JSON.stringify(this.diaInicio));
      let fecha_edit = diaInicio.split("-");
      let mes_edit = fecha_edit[1]
      let dia_edit = fecha_edit[2]
      mes_edit = parseInt(mes_edit, 10);
      dia_edit = parseInt(dia_edit, 10);
      if (mes_edit >= mes_actual && dia_edit > dia_actual) {
        let idFunc = this.findIndexInData(
          this.arrayFuncionarios,
          "id",
          this.idFuncionario
        );
        if (this.radioGroup == "1") {
          this.arrayFuncionarios[idFunc].horarios[
            this.idHorario
          ].start = `${this.diaInicio} ${this.newHoraInicio[0]}`;
          this.arrayFuncionarios[idFunc].horarios[
            this.idHorario
          ].end = `${this.diaInicio} ${this.newHoraFin[0]}`;
          this.arrayFuncionarios[idFunc].horarios[this.idHorario].idTipoTurno =
            "1";
          this.arrayFuncionarios[idFunc].horarios[this.idHorario].color =
            "green";

          let rep = this.turnosActualizados.some(
            (turno) =>
              turno.idTurno ==
              this.arrayFuncionarios[idFunc].horarios[this.idHorario].idTurno
          );
          if (rep) {
            let turno = this.findIndexInData(
              this.turnosActualizados,
              "idTurno",
              this.arrayFuncionarios[idFunc].horarios[this.idHorario].idTurno
            );
            this.turnosActualizados.splice(turno, 1);
            this.turnosActualizados.push(
              this.arrayFuncionarios[idFunc].horarios[this.idHorario]
            );
          } else {
            this.turnosActualizados.push(
              this.arrayFuncionarios[idFunc].horarios[this.idHorario]
            );
          }
        } else if (this.radioGroup == "2") {
          let spl = this.diaInicio.split("-");
          let splpop = spl.pop();
          let num = parseInt(splpop) + 1;
          let numstring = num.toString();
          if (numstring.length === 1) {
            numstring = `0${numstring}`;
          }
          spl.push(numstring);
          let diaFin = spl.join("-");
          this.arrayFuncionarios[idFunc].horarios[
            this.idHorario
          ].start = `${this.diaInicio} ${this.newHoraInicio[1]}`;
          this.arrayFuncionarios[idFunc].horarios[
            this.idHorario
          ].end = `${diaFin} ${this.newHoraFin[1]}`;
          this.arrayFuncionarios[idFunc].horarios[this.idHorario].idTipoTurno =
            "2";
          this.arrayFuncionarios[idFunc].horarios[this.idHorario].color =
            "light-blue darken-4";
          this.turnosActualizados.push(
            this.arrayFuncionarios[idFunc].horarios[this.idHorario]
          );

          let rep = this.turnosActualizados.some(
            (turno) =>
              turno.idTurno ==
              this.arrayFuncionarios[idFunc].horarios[this.idHorario].idTurno
          );
          if (rep) {
            let turno = this.findIndexInData(
              this.turnosActualizados,
              "idTurno",
              this.arrayFuncionarios[idFunc].horarios[this.idHorario].idTurno
            );
            this.turnosActualizados.splice(turno, 1);
            this.turnosActualizados.push(
              this.arrayFuncionarios[idFunc].horarios[this.idHorario]
            );
          } else {
            this.turnosActualizados.push(
              this.arrayFuncionarios[idFunc].horarios[this.idHorario]
            );
          }
        } else if (this.radioGroup == "3") {
          this.arrayFuncionarios[idFunc].horarios[
            this.idHorario
          ].start = `${this.diaInicio} ${this.newHoraInicio[2]}`;
          this.arrayFuncionarios[idFunc].horarios[
            this.idHorario
          ].end = `${this.diaInicio} ${this.newHoraFin[2]}`;
          this.arrayFuncionarios[idFunc].horarios[this.idHorario].idTipoTurno =
            "3";
          this.arrayFuncionarios[idFunc].horarios[this.idHorario].color =
            "#9a67ea";

          let rep = this.turnosActualizados.some(
            (turno) =>
              turno.idTurno ==
              this.arrayFuncionarios[idFunc].horarios[this.idHorario].idTurno
          );
          if (rep) {
            let turno = this.findIndexInData(
              this.turnosActualizados,
              "idTurno",
              this.arrayFuncionarios[idFunc].horarios[this.idHorario].idTurno
            );
            this.turnosActualizados.splice(turno, 1);
            this.turnosActualizados.push(
              this.arrayFuncionarios[idFunc].horarios[this.idHorario]
            );
          } else {
            this.turnosActualizados.push(
              this.arrayFuncionarios[idFunc].horarios[this.idHorario]
            );
          }
        } else if (this.radioGroup == "4") {
          this.arrayFuncionarios[idFunc].horarios[
            this.idHorario
          ].start = `${this.diaInicio} ${this.newHoraInicio[3]}`;
          this.arrayFuncionarios[idFunc].horarios[
            this.idHorario
          ].end = `${this.diaInicio} ${this.newHoraFin[3]}`;
          this.arrayFuncionarios[idFunc].horarios[this.idHorario].idTipoTurno =
            "4";
          this.arrayFuncionarios[idFunc].horarios[this.idHorario].color =
            "deep-orange darken-4";

          let rep = this.turnosActualizados.some(
            (turno) =>
              turno.idTurno ==
              this.arrayFuncionarios[idFunc].horarios[this.idHorario].idTurno
          );
          if (rep) {
            let turno = this.findIndexInData(
              this.turnosActualizados,
              "idTurno",
              this.arrayFuncionarios[idFunc].horarios[this.idHorario].idTurno
            );
            this.turnosActualizados.splice(turno, 1);
            this.turnosActualizados.push(
              this.arrayFuncionarios[idFunc].horarios[this.idHorario]
            );
          } else {
            this.turnosActualizados.push(
              this.arrayFuncionarios[idFunc].horarios[this.idHorario]
            );
          }
        } else if (this.radioGroup == "5") {
          this.arrayFuncionarios[idFunc].horarios[
            this.idHorario
          ].start = `${this.diaInicio} ${this.newHoraInicio[4]}`;
          this.arrayFuncionarios[idFunc].horarios[
            this.idHorario
          ].end = `${this.diaInicio} ${this.newHoraFin[4]}`;
          this.arrayFuncionarios[idFunc].horarios[this.idHorario].idTipoTurno =
            "5";
          this.arrayFuncionarios[idFunc].horarios[this.idHorario].color =
            "yellow";

          let rep = this.turnosActualizados.some(
            (turno) =>
              turno.idTurno ==
              this.arrayFuncionarios[idFunc].horarios[this.idHorario].idTurno
          );
          if (rep) {
            let turno = this.findIndexInData(
              this.turnosActualizados,
              "idTurno",
              this.arrayFuncionarios[idFunc].horarios[this.idHorario].idTurno
            );
            this.turnosActualizados.splice(turno, 1);
            this.turnosActualizados.push(
              this.arrayFuncionarios[idFunc].horarios[this.idHorario]
            );
          } else {
            this.turnosActualizados.push(
              this.arrayFuncionarios[idFunc].horarios[this.idHorario]
            );
          }
        }
        this.dialogItem = false;
      } else {
        this.snackbarChanges1 = true;
      }
    },
    findIndexInData(data, property, value) {
      var result = -1;
      data.some(function(item, i) {
        if (item[property] === value) {
          result = i;
          return true;
        }
      });
      return result;
    },
    actualizarTurnos() {
      if (this.turnosActualizados.length != 0) {
        let turnos = [];
        let turnosActualizados = JSON.parse(
          JSON.stringify(this.turnosActualizados)
        );
        turnosActualizados.forEach((element) => {
          let fechaInicio = element.start.split(" ");
          let fechaFin = element.end.split(" ");
          element.start = fechaInicio[0];
          element.end = fechaFin[0];
          turnos.push(element);
        });
        console.log(turnos);
        this.axios
          .put("/modificarturnos", {
            data: {
              turnos: turnos,
            },
          })
          .then((response) => {
            // Respuesta del servidor
            if (this.turnosBorrados.length != 0) {
              let turnos = [];
              let turnosBorrados = JSON.parse(
                JSON.stringify(this.turnosBorrados)
              );
              turnosBorrados.forEach((element) => {
                let fechaInicio = element.start.split(" ");
                let fechaFin = element.end.split(" ");
                element.start = fechaInicio[0];
                element.end = fechaFin[0];
                turnos.push(element);
              });
              this.axios
                .delete("/borrarturnos", {
                  data: {
                    turnos: turnos,
                  },
                })
                .then((response) => {
                  // Respuesta del servidor
                })
                .catch((e) => {
                  console.log(e);
                });
            }
            this.actualizarCalendario();
            this.dialogVentana = false;
            this.snackbar = true;
          })
          .catch((e) => {
            console.log(e);
          });
      } else if (this.turnosBorrados.length != 0) {
        let turnos = [];
        let turnosBorrados = JSON.parse(JSON.stringify(this.turnosBorrados));
        turnosBorrados.forEach((element) => {
          let fechaInicio = element.start.split(" ");
          let fechaFin = element.end.split(" ");
          element.start = fechaInicio[0];
          element.end = fechaFin[0];
          turnos.push(element);
        });
        console.log(turnos);
        this.axios
          .delete("/borrarturnos", {
            data: {
              turnos: turnos,
            },
          })
          .then((response) => {
            // Respuesta del servidor
            this.actualizarCalendario();
            this.dialogVentana = false;
            this.snackbar = true;
          })
          .catch((e) => {
            console.log(e);
          });
      } else {
        this.dialogVentana = false;
      }
    },
    actualizarCalendario() {
      let eventos = [];
      this.arrayFuncionarios.forEach((element) => {
        element.horarios.forEach((element2) => {
          eventos.push(element2);
        });
      });
      this.$store.dispatch("updateEventos", eventos);
    },
    tipoTurnosQuery() {
      this.axios
        .get("/tiposturnos")
        .then((response) => {
          // Respuesta del servidor
          response.data.forEach((element) => {
            this.newHoraInicio.push(element.HoraInicio);
            this.newHoraFin.push(element.HoraFin);
          });
          console.log(this.newHoraInicio);
          console.log(this.newHoraFin);
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
  computed: {
    ...mapState(["eventos", "funcionarios", "backup_eventos"]),
  },
  mounted() {
    this.tipoTurnosQuery();
  },
};
</script>

<style></style>
