<template>
  <v-form
    lazy-validations
    style="padding-left: 40px;
    padding-right: 40px;"
    @submit.prevent="verificacionInfo()"
  >
    <h1
      class="hidden-sm-and-down font-weight-bold mb-4"
      style="padding-top: 20px"
    >
      Ingresa los datos
    </h1>
    <h1
      class="hidden-md-and-up font-weight-bold mb-4"
      style="padding-top: 20px"
    >
      Registrar funcionarios
    </h1>

    <v-text-field
      label="Primer Nombre"
      v-model="nombre"
      required
    ></v-text-field>

    <v-text-field
      label="Primer Apellido"
      v-model="apellido"
      required
    ></v-text-field>

    <v-text-field label="Cdula" v-model="cedula" required></v-text-field>

    <v-select
      v-model="area"
      :items="areas"
      item-text="Area"
      item-value="idAreas"
      label="rea o servicio"
      required
    ></v-select>

    <v-select
      v-model="centro"
      :items="centros"
      label="Centro de costo"
      required
    ></v-select>

    <v-select
      :items="cargos"
      v-model="cargo"
      item-text="Cargo"
      item-value="idCargo"
      label="Cargo del funcionario"
      required
    ></v-select>

    <v-select
      :items="contratos"
      v-model="contrato"
      item-text="Contrato"
      item-value="idTipoContrato"
      label="Tipo de contrato"
      required
    ></v-select>

    <v-btn
      type="submit"
      class="mt-7 mb-7"
      block
      color="success"
      style="color: white"
    >
      Registrar
    </v-btn>

    <v-snackbar v-model="snackbar" :timeout="timeout" color="#0061aa">
      {{ text }}
      <template v-slot:action="{ attrs }">
        <v-btn color="white" text v-bind="attrs" @click="snackbar = false">
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>
  </v-form>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { required, alpha, helpers, numeric } from "vuelidate/lib/validators";
const alphaCompleto = helpers.regex("alpha", /^([^\u0000-\u007F]|[A-Za-z])+$/);
export default {
  data() {
    return {
      nombre: "",
      apellido: "",
      cedula: "",
      area: "",
      centro: "",
      cargo: "",
      contrato: "",
      snackbar: false,
      text: "",
      timeout: 2000,
      areas: [],
      cargos: [
        { idCargo: 1, Cargo: "Jefe" },
        { idCargo: 2, Cargo: "Auxiliar" },
      ],
      centros: [
        "HA001",
        "HA002",
        "HA003",
        "HA004",
        "HA005",
        "HA010",
        "HA013",
        "HA014",
        "HA018",
        "HA021",
        "HA023",
        "HM102",
      ],
      contratos: [
        { idTipoContrato: 1, Contrato: "Tiempo completo" },
        { idTipoContrato: 2, Contrato: "Medio tiempo" },
      ],
    };
  },
  mounted() {
    this.areasQuery();
  },
  validations: {
    nombre: { required, alphaCompleto },
    apellido: { required, alphaCompleto },
    cedula: { required, numeric },
    area: { required },
    centro: { required },
    cargo: { required },
    contrato: { required },
  },
  methods: {
    ...mapActions(["crearUsuario"]),
    areasQuery() {
      this.axios
        .get("/areas")
        .then((response) => {
          // Respuesta del servidor
          this.areas = response.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    verificacionInfo() {
      if (
        !this.$v.nombre.required &&
        !this.$v.apellido.required &&
        !this.$v.cedula.required &&
        !this.$v.area.required &&
        !this.$v.centro.required &&
        !this.$v.cargo.required
      ) {
        this.text = "Todos los campos deben ser ingresados";
        this.snackbar = true;
        return;
      } else if (!this.$v.nombre.required) {
        this.text = "El nombre del funcionario debe ser ingresado";
        this.snackbar = true;
        return;
      } else if (!this.$v.apellido.required) {
        this.text = "El apellido del funcionario debe ser ingresado";
        this.snackbar = true;
        return;
      } else if (!this.$v.cedula.required) {
        this.text = "La cdula del funcionario debe ser ingresada";
        this.snackbar = true;
        return;
      } else if (!this.$v.area.required) {
        this.text = "Debe ingresar el rea o servicio del funcionario";
        this.snackbar = true;
        return;
      } else if (!this.$v.centro.required) {
        this.text = "Debe ingresar el centro de costo del funcionario";
        this.snackbar = true;
        return;
      } else if (!this.$v.cargo.required) {
        this.text = "Debe ingresar el cargo del funcionario";
        this.snackbar = true;
        return;
      } else if (!this.$v.contrato.required) {
        this.text = "Debe ingresar el tipo de contrato del funcionario";
        this.snackbar = true;
        return;
      } else if (
        !this.$v.nombre.alphaCompleto ||
        !this.$v.apellido.alphaCompleto
      ) {
        this.text =
          "El nombre y apellido del funcionario no deben contener espacios ni otro tipo de caracteres especiales";
        this.snackbar = true;
        return;
      } else if (!this.$v.cedula.numeric) {
        this.text =
          "La cdula del funcionario no debe contener letras ni caracteres especiales";
        this.snackbar = true;
        return;
      } else {
        this.axios
          .get("/verificarcedula", { params: { cedula: this.cedula } })
          .then((response) => {
            console.log(response.data)
            if (response.data[0].booleano == 0) {
              this.axios
                .post("/registro", {
                  nombre: this.$v.nombre.$model,
                  apellido: this.$v.apellido.$model,
                  cedula: this.$v.cedula.$model,
                  area_servicio: this.$v.area.$model,
                  centro_costo: this.$v.centro.$model,
                  cargo: this.$v.cargo.$model,
                  contrato: this.$v.contrato.$model,
                  estado: 1,
                })
                .then((response) => {
                  // Respuesta del servidor
                  this.snackbar = true;
                  this.nombre = "";
                  this.apellido = "";
                  this.cedula = "";
                  this.area = "";
                  this.centro = "";
                  this.cargo = "";
                  this.contrato = "";
                  this.text =
                    "El registro del funcionario se ha realizado correctamente";
                })
                .catch((e) => {
                  console.log(e);
                });
            } else {
              this.text = "El nmero de cdula ingresado ya se encuentra registrado. Verifique los datos ingresados";
              this.snackbar = true;
            }
          })
          .catch((e) => {
            console.log(e);
          });
      }
    },
  },
  computed: {},
};
</script>

<style></style>
