<template>
  <v-container>
    <Toolbar />
    <h1 class="mt-3" style="text-align: center">Bienvenido a Bookable!</h1>
    <p class="lead description" style="text-align: center">
      En esta seccin, podrs seleccionar el rea o servicio para generar los
      turnos
    </p>
    <v-row style="margin-top: 30px">
      <v-col sm="6" md="4" lg="4">
        <DashboardCard
          v-bind:title="titles[11]"
          v-bind:activos="activos[11]"
        ></DashboardCard>
      </v-col>
      <v-col
        v-for="(areanueva, index) in areasnuevas"
        :key="areanueva.Area"
        sm="6"
        md="4"
        lg="4"
      >
        <v-card style="position: relative">
          <v-img :src="img[0].img" height="150px"></v-img>
          <div
            style="display: flex; align-items: center; justify-content: space-between"
          >
            <div>
              <v-card-title>
                {{ areasnuevas[index].Area }}
              </v-card-title>
              <v-card-subtitle v-if="areasnuevas.length == activosnuevas.length">
                Funcionarios registrados:
                {{ activosnuevas[index].Cantidad_registrados }}
                <br />
                Funcionarios activos:
                {{ activosnuevas[index].Cantidad_activos }}
              </v-card-subtitle>
              <v-card-subtitle v-else>
                Funcionarios registrados:
                0
                <br />
                Funcionarios activos:
                0
              </v-card-subtitle>
            </div>
            <div style="padding-right: 15px">
              <v-icon style="color: black">mdi-arrow-right</v-icon>
            </div>
          </div>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import Calendar from "../components/Calendar.vue";
import Toolbar from "../components/Toolbar.vue";
import DashboardCard from "../components/DashboardCard.vue";

export default {
  name: "App",
  components: {
    Calendar,
    Toolbar,
    DashboardCard,
  },
  data: () => ({
    images: [],
    activos: [],
    titles: [],
    areasnuevas: [],
    activosnuevas: [],
    img: [{ img: require("../assets/warning.png") }],
  }),
  methods: {
    funcionariosActivos() {
      this.axios
        .get("/funactivos")
        .then((response) => {
          // Respuesta del servidor
          console.log(response.data);
          this.activos = response.data;
          for (let i = 13; i < response.data.length; i++) {
            this.activosnuevas.push(response.data[i]);
          }
          console.log(this.activosnuevas);
        })
        .catch((e) => {
          console.log(e);
        });
    },
    cardsAreas() {
      this.titles.push(
        { area: "Piso 1", img: require("../assets/piso1.jpg"), ruta: "/piso1" },
        { area: "Piso 2", img: require("../assets/piso2.jpg"), ruta: "/piso2" },
        {
          area: "UCI Adulto",
          img: require("../assets/uci-adulto.jpg"),
          ruta: "/uciadulto",
        },
        {
          area: "UCI Respiratoria",
          img: require("../assets/uci-respiratoria.jpg"),
          ruta: "/respiratoria",
        },
        {
          area: "UCI Neonatal",
          img: require("../assets/uci-neonatal.jpg"),
          ruta: "/ucineo",
        },
        {
          area: "Urgencia General",
          img: require("../assets/urg-general.jpg"),
          ruta: "/urgeneral",
        },
        {
          area: "Urgencia Respiratoria",
          img: require("../assets/urg-respiratoria.jpg"),
          ruta: "/urgrespiratoria",
        },
        {
          area: "Reanimacin",
          img: require("../assets/reanimacion.jpg"),
          ruta: "/reanimacion",
        },
        {
          area: "Ciruga",
          img: require("../assets/cirugia.jpg"),
          ruta: "/cirugia",
        },
        {
          area: "Consulta Externa",
          img: require("../assets/cons-externa.jpg"),
          ruta: "/consultaexterna",
        },
        {
          area: "Varios",
          img: require("../assets/varios.jpg"),
          ruta: "/varios",
        },
        {
          area: "Auxiliares Clnicos",
          img: require("../assets/aux-clinicos.jpg"),
          ruta: "/auxclinicos",
        },
        {
          area: "Patrulla No COVID",
          img: require("../assets/patrulla-no-covid.jpg"),
          ruta: "/nocovid",
        }
      );
    },
    areasNuevas() {
      this.axios
        .get("/areasnuevas")
        .then((response) => {
          // Respuesta del servidor
          console.log(response.data);
          this.areasnuevas = response.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
  mounted() {
    this.funcionariosActivos();
    this.cardsAreas();
    this.areasNuevas();
  },
};
</script>

