<template>
  <v-form
    lazy-validations
    style="padding-left: 40px;
    padding-right: 40px;"
    @submit.prevent="changeShifts()"
  >
    <h1
      class="hidden-sm-and-down font-weight-bold mb-4"
      style="padding-top: 20px"
    >
      Ingresa los datos
    </h1>
    <h1
      class="hidden-md-and-up font-weight-bold mb-4"
      style="padding-top: 20px"
    >
      Cambiar duracin de turnos
    </h1>

    <v-select
      v-model="tipoturno"
      :items="tiposturnos"
      item-text="Nombre"
      item-value="idTipoTurnos"
      label="Tipo de turno"
      required
    ></v-select>

    <v-dialog
      ref="dialog"
      v-model="modal1"
      :return-value.sync="time"
      persistent
      width="290px"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-text-field
          v-model="start"
          label="Nueva hora inicio del turno"
          prepend-icon="mdi-clock-outline"
          readonly
          v-bind="attrs"
          v-on="on"
        ></v-text-field>
      </template>
      <v-time-picker
        v-if="modal1"
        v-model="start"
        full-width
        format="24hr"
      >
        <v-spacer></v-spacer>
        <v-btn text color="primary" @click="modal1 = false">
          Cancelar
        </v-btn>
        <v-btn text color="primary" @click="modal1 = false">
          Aceptar
        </v-btn>
      </v-time-picker>
    </v-dialog>

    <v-dialog
      ref="dialog"
      v-model="modal2"
      :return-value.sync="time"
      persistent
      width="290px"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-text-field
          v-model="end"
          label="Nueva hora fin del turno"
          prepend-icon="mdi-clock-outline"
          readonly
          v-bind="attrs"
          v-on="on"
        ></v-text-field>
      </template>
      <v-time-picker
        v-if="modal2"
        v-model="end"
        full-width
        format="24hr"
      >
        <v-spacer></v-spacer>
        <v-btn text color="primary" @click="modal2 = false">
          Cancelar
        </v-btn>
        <v-btn text color="primary" @click="modal2 = false">
          Aceptar
        </v-btn>
      </v-time-picker>
    </v-dialog>

    <v-btn
      type="submit"
      class="mt-7 mb-7"
      block
      color="#008dca"
      style="color: white"
    >
      Enviar cambios
    </v-btn>

    <v-snackbar v-model="snackbar" :timeout="timeout" color="#0061aa">
      {{ text }}
      <template v-slot:action="{ attrs }">
        <v-btn color="white" text v-bind="attrs" @click="snackbar = false">
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>
  </v-form>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { required } from "vuelidate/lib/validators";
export default {
  data() {
    return {
      modal1: false,
      modal2: false,
      tipoturno: "",
      start: "",
      end: "",
      snackbar: false,
      text: "",
      timeout: 2000,
      tiposturnos: [],
    };
  },
  validations: {
    tipoturno: { required },
    start: { required },
    end: { required },
  },
  mounted() {
    this.tipoTurnosQuery();
  },
  methods: {
    ...mapActions(["crearUsuario"]),
    tipoTurnosQuery() {
      this.axios
        .get("/tiposturnos2")
        .then((response) => {
          // Respuesta del servidor
          this.tiposturnos = response.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    changeShifts() {
      if (
        !this.$v.tipoturno.required ||
        !this.$v.start.required ||
        !this.$v.end.required
      ) {
        this.text = "Debe ingresar todos los campos";
        this.snackbar = true;
        return;
      } else {
        this.axios
          .put("/modificartiposturnos", {
            data: {
              id: this.$v.tipoturno.$model,
              start: this.$v.start.$model,
              end: this.$v.end.$model,
            },
          })
          .then((response) => {
            // Respuesta del servidor
            this.text =
              "La duracin del turno ha sido modificada correctamente";
            this.snackbar = true;
            this.tipoturno = "";
            this.start = "";
            this.end = "";
          })
          .catch((e) => {
            console.log(e);
          });
      }
    },
  },
};
</script>

<style></style>
