<template>
  <v-card color="#0061aa" flat tile>
    <v-app-bar app dark color="#0061aa">
      <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title id="hola">Bookable</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn dark icon class="mr-5" @click="$store.commit('setHelper', true)">
        <v-icon>mdi-help</v-icon>
      </v-btn>
      <v-menu bottom left>
        <template v-slot:activator="{ on, attrs }">
          <v-btn dark icon v-bind="attrs" v-on="on">
            <v-icon>mdi-logout</v-icon>
          </v-btn>
        </template>
        <v-list>
          <v-list-item @click="cerrarSesion">
            <v-list-item-title>Cerrar sesin</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-app-bar>

    <v-navigation-drawer v-model="drawer" fixed dark temporary color="#0061aa">
      <v-col style="text-align: center">
        <v-flex class="mt-5">
          <v-avatar size="100">
            <v-img src="../assets/prescripcion.png"></v-img>
          </v-avatar>
          <p class="white--text subheading mt-1">
            Coordinador
          </p>
        </v-flex>
      </v-col>

      <v-divider></v-divider>

      <v-list dense>
        <v-list-item router :to="'/dashboard'">
          <v-list-item-icon>
            <v-icon>mdi-home</v-icon>
          </v-list-item-icon>
          <v-list-item-title>Inicio</v-list-item-title>
        </v-list-item>
        <v-list-group prepend-icon="mdi-lan" no-action>
          <template v-slot:activator>
            <v-list-item-title>Histricos</v-list-item-title>
          </template>
          <v-list-item
            v-for="admin in admins"
            :key="admin.text"
            link
            router
            :to="admin.route"
          >
            <v-list-item-title>{{ admin.text }}</v-list-item-title>
          </v-list-item>
        </v-list-group>
        <v-list-item router :to="'/registrofuncionario'">
          <v-list-item-icon>
            <v-icon>mdi-account-arrow-right</v-icon>
          </v-list-item-icon>
          <v-list-item-title>Registrar funcionarios</v-list-item-title>
        </v-list-item>
        <v-list-item router :to="'/deshabilitarfuncionario'">
          <v-list-item-icon>
            <v-icon>mdi-account-cancel</v-icon>
          </v-list-item-icon>
          <v-list-item-title>Deshabilitar funcionarios</v-list-item-title>
        </v-list-item>
        <v-list-item router :to="'/modificarfuncionario'">
          <v-list-item-icon>
            <v-icon>mdi-account-edit</v-icon>
          </v-list-item-icon>
          <v-list-item-title>Modificar funcionarios</v-list-item-title>
        </v-list-item>
        <v-list-item router :to="'/creararea'">
          <v-list-item-icon>
            <v-icon>mdi-calendar-plus</v-icon>
          </v-list-item-icon>
          <v-list-item-title>Crear nueva rea</v-list-item-title>
        </v-list-item>
      </v-list>
      <v-divider></v-divider>
    </v-navigation-drawer>

    <v-dialog persistent v-model="helper" scrollable max-width="300px">
      <v-card>
        <v-card-title>Cdigo de colores</v-card-title>
        <v-divider></v-divider>
        <v-list>
          <v-subheader>Tipos de turnos</v-subheader>
          <v-list-item>
            <v-list-item-icon>
              <v-icon color="green" large>mdi-square-rounded</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Corrido ({{horas[0]}})</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-icon>
              <v-icon color="light-blue darken-4" large
                >mdi-square-rounded</v-icon
              >
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Noche ({{horas[1]}})</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-icon>
              <v-icon style="color: #9a67ea" large>mdi-square-rounded</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>JC ({{horas[2]}})</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-icon>
              <v-icon color="deep-orange darken-4" large
                >mdi-square-rounded</v-icon
              >
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Maana ({{horas[3]}})</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list>
        <v-card-actions>
          <v-btn
            color="blue darken-1"
            text
            @click="$store.commit('setHelper', false)"
          >
            Cerrar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
import { mapActions, mapMutations, mapState } from "vuex";
import Dialog from "./Dialog";
export default {
  components: { Dialog },
  data: () => ({
    drawer: false,
    admins: [
      // { text: "Piso 1", route: "/historicopiso1" },
      // { text: "Piso 2", route: "/historicopiso2" },
      // { text: "UCI Adulto", route: "/historicouciadulto" },
      // { text: "UCI Respiratoria", route: "/historicorespiratoria" },
      // { text: "UCI Neonatal", route: "/historicoucineo" },
      // { text: "URG General", route: "/historicourgeneral" },
      // { text: "URG Respiratoria", route: "/historicourgrespiratoria" },
      // { text: "Reanimacin", route: "/historicoreanimacion" },
      // { text: "Ciruga", route: "/historicocirugia" },
      // { text: "Consulta externa", route: "/historicoconsultaexterna" },
      // { text: "Varios", route: "/historicovarios" },
      { text: "Aux. Clnicos", route: "/historicoauxclinicos" },
      // { text: "Patrulla No COVID", route: "/historiconocovid" },
    ],
    horas: []
  }),
  methods: {
    ...mapActions(["cerrarSesion"]),
    ...mapMutations(["setHelper"]),
    tipoTurnosQuery() {
      this.axios
        .get("/tiposturnos")
        .then((response) => {
          // Respuesta del servidor
          response.data.forEach((element) => {
            let horainicio = element.HoraInicio.slice(0,5)
            let horafin = element.HoraFin.slice(0,5)
            this.horas.push(` ${horainicio} - ${horafin} `);
          });
          console.log(this.horas);
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
  computed: {
    ...mapState(["helper"]),
  },
  mounted(){
    this.tipoTurnosQuery();
  }
};
</script>

<style>
#hola {
  font-family: "Varela Round", sans-serif;
}
</style>
